/* multiple platform section */
.platform {
  background: radial-gradient(rgba(188, 43, 80, 0.3) 30%, rgba(130, 84, 235, 0.3));
  text-align: center;
  padding-top: 80px;
  padding-bottom: 80px; }
  .platform .content {
    margin-bottom: 42.64px; }
  .platform .pic {
    display: flex;
    justify-content: center; }
    .platform .pic li {
      width: 264px;
      height: 264px;
      background: #bc2b50;
      position: relative;
      display: flex;
      border-radius: 20px;
      box-shadow: 0 7px 20px rgba(0, 0, 0, 0.5);
      overflow: hidden;
      margin-right: 48px; }
      .platform .pic li .shadow {
        width: 100px;
        height: 100px;
        background: rgba(0, 0, 0, 0.4);
        box-shadow: 0 0 19px 20px rgba(0, 0, 0, 0.4);
        -webkit-transform: scale(0.85);
        transform: scale(1);
        position: absolute;
        right: -35px;
        bottom: -35px;
        border-radius: 50px; }
      .platform .pic li img {
        object-fit: cover;
        width: 120%;
        transition: .3s; }
      .platform .pic li:nth-child(1) img {
        transform: scale(1.2) translateY(10px) translateX(10px); }
      .platform .pic li:nth-child(2) img {
        transform: scale(1.2) translateY(29px) translateX(28px); }
      .platform .pic li:nth-child(3) img {
        transform: scale(1.4) translateY(12px) translateX(8px); }
      .platform .pic li:nth-child(4) {
        margin-right: 0; }
        .platform .pic li:nth-child(4) img {
          transform: scale(1.4) translateY(11px) translateX(38px); }
      .platform .pic li .text {
        position: absolute;
        top: 0;
        left: 0;
        width: 264px;
        height: 264px;
        display: flex;
        align-items: flex-end;
        text-align: left;
        transition: .3s; }
        .platform .pic li .text h2 {
          margin-bottom: 0;
          font-size: 20px;
          line-height: 27.24px;
          width: 200px;
          margin: 0 auto;
          margin-bottom: 31.68px;
          color: rgba(0, 0, 0, 0);
          transition: .5s; }
      .platform .pic li:hover:nth-child(1) img {
        transform: scale(1.3) translateY(10px) translateX(10px); }
      .platform .pic li:hover:nth-child(2) img {
        transform: scale(1.3) translateY(29px) translateX(28px); }
      .platform .pic li:hover:nth-child(3) img {
        transform: scale(1.5) translateY(12px) translateX(8px); }
      .platform .pic li:hover:nth-child(4) img {
        transform: scale(1.5) translateY(11px) translateX(38px); }
      .platform .pic li:hover .text {
        background: rgba(0, 0, 0, 0.3); }
        .platform .pic li:hover .text h2 {
          color: #fff; }

